.product-list {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
  }
  
  .product-list-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 20px;
    cursor: pointer;
  }
  
  .product-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
  }
  
  .product-image {
    height: 200px;
    overflow: hidden;
    margin-bottom: 20px;
  }
  
  .product-image img {
    max-width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .product-info {
    text-align: center;
  }
  
  .product-info h2 {
    font-size: 24px;
    margin-bottom: 10px;
  }
  
  .product-description {
    font-size: 16px;
    margin-bottom: 5px;
  }
  
  .product-price {
    font-size: 20px;
    font-weight: bold;
    margin-top: 10px;
  }